import {
  history,
  devicetype,
  devicename,
  deviceattr,
} from '../../utils/service';

import historyDataEcharts from './historyDataEcharts.vue';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  components: {
    historyDataEcharts,
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling

  },
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      value2: '',
      value: '',
      datalist: '',
      devicename: '', // 设备名称
      devicetype: '', // 设备类型
      deviceattr: '', // 设备属性
      timedata: '',
      sdevicename: '', // 确认设备名称
      sdevicetype: '', // 确认设备类型
      sdeviceattr: '', // 确认设备属性
      pageSizes: [10, 20, 100],
      displayMode: "full",
      showPageSizeSelector: true,
      showInfo: true,
      showNavButtons: true,
      edata: '',
      title: '',
      total: 0,
      pagesize: 20,
      clientHeight: '',
    };
  },

  mounted() {
    history({}, res => {
      this.total = res.count
      this.datalist = res.data
    });
    this.getdevicetype()
    this.clientHeight = document.documentElement.clientHeight - 250
  },
  methods: {
    handleCurrentChange(e) {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        deviceid: this.sdevicename,
        devicetype: this.sdevicetype,
        pageindex: e,
        pagesize: this.pagesize,
      }
      history(formData, res => {
        this.total = res.count
        var title = res.data.columns
        title = title.filter(item => item.Field != 'id' && item.Field != 'device_id' && item.Field != 'wavetype' && item.Field != 'wavedata' && item.Field != 'wavebase64')
        this.title = title
        this.datalist = res.data.data
        console.log('this.datalist :>> ', this.title, this.datalist);
      });
    },
    topsure() {
      this.datalist = ''
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        deviceid: this.sdevicename,
        devicetype: this.sdevicetype,
      }
      history(formData, res => {
        this.total = res.count
        var title = res.data.columns
        title = title.filter(item => item.Field != 'id' && item.Field != 'device_id' && item.Field != 'wavetype' && item.Field != 'wavedata' && item.Field != 'wavebase64')
        if (title) {
          this.title = title
        }
        this.datalist = res.data.data
        if (this.datalist) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }

      });
    },
    // 获取设备类型信息
    getdevicetype() {
      devicetype({}, res => {

        this.devicetype = res.data

      });
    },
    // 根据设备类型获取设备名称
    changesdevicetype(e) {
      this.sdevicetype = e
      var data = {
        devicetype: this.sdevicetype,
      }
      devicename(data, res => {
        this.sdevicename = ''
        this.devicename = res.data
      });
    },
    changesdevicename(e) {
      this.sdevicename = e
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
  }
};



