<template>
    <div class="historyData bg">
        <div class="title">
            <span></span>
            <div class="title_name">分接开关电机工作特性</div>
        </div>
        <!-- <el-form :inline="true" class="demo-form-inline  ">
            <el-form-item label="检测时间：">
                <el-date-picker v-model="timedata" type="datetimerange" align="right" start-placeholder="开始日期"
                    end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']" :picker-options="pickerOptions"
                    value-format="yyyy-MM-dd HH:mm:ss">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="设备类型">
                <el-select v-model="sdevicetype" clearable placeholder="请选择" @change="changesdevicetype">
                    <el-option v-for="(item, i) in devicetype" :key="i" :label="item.name" :value="item.devicetype">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="设备名称：">
                <el-select v-model="sdevicename" clearable placeholder="请选择">
                    <el-option v-for="(item, i) in devicename" :key="i" :label="item.device_name"
                        :value="item.device_id" @change="changesdevicename">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
            </el-form-item>
        </el-form> -->
        <div class="choice">
            <div class="data">
                <span>检测时间：</span>
                <el-date-picker v-model="timedata" type="datetimerange" align="right" start-placeholder="开始日期"
                    end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']" :picker-options="pickerOptions"
                    value-format="yyyy-MM-dd HH:mm:ss">
                </el-date-picker>
            </div>
            <div>
                <span>设备类型:</span>
                <el-select v-model="sdevicetype" clearable placeholder="请选择" @change="changesdevicetype">
                    <el-option v-for="(item, i) in devicetype" :key="i" :label="item.name" :value="item.devicetype">
                    </el-option>
                </el-select>
            </div>
            <div>
                <span>设备名称：</span>
                <el-select v-model="sdevicename" clearable placeholder="请选择">
                    <el-option v-for="(item, i) in devicename" :key="i" :label="item.device_name"
                        :value="item.device_id" @change="changesdevicename">
                    </el-option>
                </el-select>
            </div>
            <div>
                <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
            </div>
        </div>
        <div class="historyData_main">
            <!-- <div v-show="datalist.length == 0" class="nodata">无数据</div> -->
            <div class="table" v-show="datalist.length > 0">
                <DxDataGrid :data-source="datalist" :allow-column-resizing="true" :show-column-lines="true"
                    :auto-expand-all="false" :show-row-lines="true" :column-auto-width="true"
                    :focused-row-enabled="true" :auto-navigate-to-focused-row="true" width="98%" key-expr="key"
                    class="main_table" style="width:98%" :height="clientHeight">
                    <DxColumn alignment="center" v-for="(item1, i1) in title" :key="i1" :data-field="item1.Field"
                        :caption="item1.FieName" />
                    <DxPaging :page-size="50" />
                    <DxScrolling use-native="false" />
                </DxDataGrid>
            </div>
            <el-pagination :page-size="pagesize" :pager-count="5" :page-sizes="[10, 20, 30, 40]"
                @size-change="handleSizeChange" layout="total, sizes, prev, pager, next, jumper" :total="total"
                @current-change="handleCurrentChange" v-show="datalist.length != 0"
                style="margin-left: 25px;padding-left: 20px;">
            </el-pagination>
            <!-- <div>
                <historyDataEcharts :edata="edata"/>
            </div> -->
        </div>
    </div>
</template>

<script>
import historyData from "./historyData";
export default historyData;
</script>
<style scoped>
@import url("historyData.css");
@import url("../../assets/css/common.css");
</style>



